ul.recentlyClosed__area {
    width: 500px;
    display: flex;
    justify-content: center;
    align-items: start;
    flex-direction: column;
    position: absolute;
    top: 45px;
    left: -118px;
    margin: 0;
    list-style: none;
    text-align: start;
    background: #fff;
    padding: 6px 0;
    z-index: 999;
    display: none;
    box-shadow: 0px 0px 4px #0000007a;
    border-radius: 5px;
    text-shadow: none;
}
ul.recentlyClosed__area::before{
    content: '';
    position: absolute;
    top: -18px;
    left: 224px;
    border-top: 10px solid transparent;
    border-bottom: 10px solid #ffffff;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
}
ul.recentlyClosed__area li {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 10px;
}
ul.recentlyClosed__area li:hover{
    background: #f5f5f5;
}
ul.recentlyClosed__area li a {
    text-decoration: none;
    padding: 7px 14px;
    display: flex;
    width: 100%;
    color: #4c4c4c;
}
