body{
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.sticky-note {
    position: fixed;
    cursor: move;
    box-shadow: 0px 0px 12px 1px #0000004d;
    text-shadow: none;
}
.sticky-note__header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 30px;
}
/* .sticky-note__header > div{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
} */
.sticky-note__header span {
    color: #7e7e7e;
    text-decoration: none;
    font-size: 20px;
    margin: 0 5px;
    cursor: pointer;
}
.sticky-note__color-list{
    padding: 0;
    margin: 0;
    list-style: none;
    background: #e7e7e7;
    position: absolute;
    left: 8px;
    top: 28px;
    font-size: 19px;
    display: none;
    box-shadow: 0px 0px 3px #00000061;
    color: #7e7e7e;
}
.sticky-note__color-list li{
    padding: 8px;
    cursor: pointer;
}
.sticky-note__color-list li:hover{
    background: #00000014;
}
.sticky-note textarea{
    outline: 0;
    border: 0;
    background: #00000014;
    font-size: 21px;
    margin-bottom: -4px;
    padding: 5px;
    min-width: 110px;
    min-height: 110px;
}



.sticky-note__btns {
    position: absolute;
    bottom: 12px;
    right: 101px;
}

.sticky-note__btns>span {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2px;
    cursor: pointer;
    flex-direction: column;
    color: #fff;
    gap: 5px;
}

.sticky-note__btns >span>div{
    display: flex;
    justify-content: center;
    align-items: center;
}



ul.stickyNoteMenu{
    width: 100px;
    padding: 0;
    margin: 0;
    background: #fff;
    list-style: none;
    text-shadow: none;
    position: absolute;
    bottom: 25px;
    left: 7px;
    color: #444;
    border-radius: 7px;
    overflow: hidden;
    display: none;
}
ul.stickyNoteMenu li{
    padding: 9px 13px;
}
ul.stickyNoteMenu li:hover{
    background: rgb(158, 158, 158);
}
ul.stickyNoteMenu .sticky-note__show-all{
    display: none;
}