.sideBarLink__area{
    position: absolute;
    top: 50%;
    left: 0px;
    transform: translate(0px, -50%);
    background: #fff;
    border-radius: 0 5px 5px 0;
    box-shadow: 0px 0px 4px #0000007a;
    z-index: 999;
    text-shadow: none;
    list-style: none;
    padding: 0 8px;
    margin: 0;
    overflow: hidden;
}

