.app-links__area{
    position: absolute;
    top: 45px;
    left: 17px;
    background: #fff;
    border-radius: 5px;
    box-shadow: 0px 0px 4px #0000007a;
    z-index: 999;
    display: none;
    text-shadow: none;
}
.app-links__area::before{
    content: '';
    position: absolute;
    top: -18px;
    left: 2px;
    border-top: 10px solid transparent;
    border-bottom: 10px solid #ffffff;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
}
.app-links__area ul{
    list-style: none;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 7px;
    max-height: 62vh;
    overflow: auto;
    padding: 12px;
    text-transform: capitalize;
}
.app-links__area li {
    
}

.app-links__area li a{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 4px;
    text-decoration: none;
    color: #3c3c3c;
    padding: 5px 6px;
}
.app-links__area li a:hover {
    border-color: #e5e5e5;
    background-color: #ebf4fa;
    box-shadow: 0px 0px 1px 1px #00000024;
    border-radius: 5px;
}
.app-links__area li a img{
    width: 46px;
}


